.user__detail {
  width: $w-100 - 75;
  padding-right: $p-2;
  border-right: 1px solid $grey-2;
  .detail__acc {
    width: $w-100;
    margin-bottom: $m-2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      width: $w-10 * 2;
      height: $h-10 * 2;
      border-radius: 50%;
      margin-bottom: $m-1;
      box-shadow: 0 0 5px $dark-2;
      @include respond(extraTablet) {
        width: $w-4;
        height: $h-4;
        margin-bottom: 0;
      }
    }
    p {
      font-size: $h3;
      font-weight: $medium;
      color: $tGrey-2;
      @include respond(extraTablet) {
        font-size: $body-1;
        margin: 0 $m-2;
      }
    }
    .detail__btn {
      width: auto;
      border: 0;
      background: transparent;
      display: none;
      outline: none;
      @include respond(extraTablet) {
        display: block;
        font-size: $body-1 + 4;
      }
      @include respond(phone) {
        display: block;
        font-size: $body-1;
      }
    }
    .admin__btn {
      text-decoration: none;
      color: $tWhite;
      background: $blue;
      padding: $p-1 - 5 $p-1;
      border-radius: 50px;
      font-size: $body-1;
      transition: all 0.2s linear;
      &:hover {
        box-shadow: 0 0 5px $dark-2;
      }
      i {
        margin-right: $m-1 - 5;
        @include respond(extraTablet) {
          margin: 0;
        }
      } 
      span {
        @include respond(extraTablet) {
          display: none;
        }
      }
      @include respond(extraTablet) {
        background: transparent;
        color: $tPrimary;
        margin: $m-1;
      }
    }
    @include respond(extraTablet) {
      flex-direction: row;
      margin: 0;
    }
  }
  .detail__info {
    .info__table {
      width: $w-100;
      border-radius: 10px;
      overflow: hidden;
      font-size: $body-1;
      tr {
        .table__title {
          font-size: $h3;
        }
        td {
          padding: $p-1;
          &:nth-child(1) {
            font-weight: $medium;
          }
        }
        &:nth-child(odd) {
          background: $grey-2;
        }
        &:nth-child(even) {
          background: $grey-1;
        }
      }
    }
    @include respond(extraTablet) {
      display: none;
    }
  }
  @include respond(extraTablet) {
    width: $w-100;
    margin-bottom: $m-1;
    border: 0;
    padding: $p-1 - 5;
    border-bottom: 1px solid $grey-2;
  }
}
